.cart-dropdown {
  width: 240px;
  height: 340px;

  position: absolute;
  top: 90px;
  right: 40px;
  padding: 20px;

  background-color: white;
  border: 1px solid black;

  display: flex;
  flex-direction: column;
  z-index: 5;

  .empty_message {
    font-size: 18px;
    margin: 50px auto;
  }

  .cart-items {
    height: 240px;
    display: flex;
    flex-direction: column;
    overflow: scroll;

    &::-webkit-scrollbar {
      display: none;
    }
  }

  button {
    margin-top: auto;
  }
}
